import { platform } from "../platform";
import { generateImageBufferFromCode, } from "./qrcode-helper";
import { getBufferFromImageData } from "./png-helper";
export class VideoEditor {
    entity;
    options;
    constructor(entity, options = {}) {
        this.entity = entity;
        this.options = options;
    }
    async set(key, value, mode = "R") {
        if (mode === "A") {
            this.entity.dynamicElements[key] = await platform.image.load(value, this.entity.filename, `dynamic.${key}`);
        }
        else {
            this.entity.images[key] = value;
        }
    }
    /**
     * 
     * @param width
     * @param height
     * @returns
     */
    async createContext(width, height) {
        if (platform.globals.env !== "h5" || "OffscreenCanvas" in globalThis) {
            return platform.getOfsCanvas({ width, height });
        }
        const { container, component } = this.options;
        if (container) {
            const result = await platform.getCanvas(container, component);
            if (result.context) {
                result.canvas.width = width;
                result.canvas.height = height;
            }
            return result;
        }
        throw new Error("Don't support OffscreenCanvas, and please provide a container");
    }
    /**
     * 
     * @param context
     * @param options
     * @returns
     */
    async setCanvas(key, context, options) {
        if (this.entity.locked)
            return;
        const { width, height } = context.canvas;
        const imageData = context.getImageData(0, 0, width, height);
        const buff = getBufferFromImageData(imageData);
        await this.set(key, new Uint8Array(buff), options?.mode);
    }
    /**
     * 
     * @param key
     * @param buff
     * @param options
     * @returns
     */
    async setImage(key, url, options) {
        if (this.entity.locked)
            return;
        if (url.startsWith('data:image')) {
            await this.set(key, url, options?.mode);
        }
        else {
            const buff = await platform.remote.fetch(url);
            await this.set(key, new Uint8Array(buff), options?.mode);
        }
    }
    /**
     * 
     * @param key
     * @param code
     * @param options
     * @returns
     */
    async setQRCode(key, code, options) {
        if (this.entity.locked)
            return;
        const buff = generateImageBufferFromCode({ ...options, code });
        await this.set(key, new Uint8Array(buff), options?.mode);
    }
}
