/**
 * CurrentPointGC
 */
export class PointPool {
    pool = [];
    static instance;
    static getInstance() {
        if (!PointPool.instance) {
            PointPool.instance = new PointPool();
        }
        return PointPool.instance;
    }
    acquire() {
        if (this.pool.length > 0) {
            return this.pool.pop();
        }
        return { x: 0, y: 0, x1: 0, y1: 0, x2: 0, y2: 0 };
    }
    release(point) {
        // 
        point.x = 0;
        point.y = 0;
        point.x1 = 0;
        point.y1 = 0;
        point.x2 = 0;
        point.y2 = 0;
        this.pool.push(point);
    }
}
