import { platform } from "../platform";
import benchmark from "../benchmark";
import render from "./render";
import { ImagePool } from "./image-pool";
const { noop } = platform;
export class Painter {
    mode;
    /**
     *  Canvas 
     * Main Screen
     */
    X = null;
    /**
     *  Context 
     * Main Context
     */
    XC = null;
    /**
     *  Canvas 
     * Secondary Screen
     */
    Y = null;
    /**
     *  Context 
     * Secondary Context
     */
    YC = null;
    /**
     * 
     */
    W;
    /**
     * 
     */
    H;
    /**
     * 
     */
    model = {};
    IM = new ImagePool();
    lastResizeKey = "";
    lastTransform;
    globalTransform;
    /**
     *
     * @param mode
     *  - poster: 
     *  - animation: 
     *  -  animation
     * @param W 
     * @param H 
     */
    constructor(mode = "animation", width = 0, height = 0) {
        this.mode = mode;
        const { dpr } = platform.globals;
        this.W = width * dpr;
        this.H = height * dpr;
    }
    setModel(type) {
        const { model } = this;
        const { env } = platform.globals;
        // set type
        model.type = type;
        // set clear
        if (type === "O" &&
            // OffscreenCanvas  Firefox 
            env === "h5" &&
            navigator.userAgent.includes("Firefox")) {
            model.clear = "CR";
        }
        else if ((type === "O" && env === "tt") || env === "alipay") {
            model.clear = "CL";
        }
        else {
            model.clear = "RE";
        }
        benchmark.line(4);
        benchmark.log("brush type", model.type);
        benchmark.log("brush clear", model.clear);
        benchmark.line(4);
    }
    /**
     * 
     * @param selector
     * @param ofsSelector
     * @param component
     */
    async register(selector, ofsSelector, component) {
        const { model, mode } = this;
        const { getCanvas, getOfsCanvas } = platform;
        const { env } = platform.globals;
        // #region set main screen implement
        // --------  ---------
        if (mode === "poster" &&
            (env !== "h5" || "OffscreenCanvas" in globalThis)) {
            const { W, H } = this;
            if (!(W > 0 && H > 0)) {
                throw new Error("Poster mode must set width and height when create Brush instance");
            }
            const { canvas, context } = getOfsCanvas({ width: W, height: H });
            this.X = canvas;
            this.XC = context;
            this.setModel("O");
        }
        else {
            const { canvas, context } = await getCanvas(selector, component);
            const { width, height } = canvas;
            // 
            this.X = canvas;
            this.XC = context;
            if (mode === "poster") {
                canvas.width = width;
                canvas.height = height;
                this.setModel("C");
            }
            else {
                this.W = width;
                this.H = height;
            }
        }
        if (env !== "h5") {
            const { X, IM } = this;
            const { createImage } = X;
            Object.defineProperty(X, "createImage", {
                get() {
                    return () => IM.getReleaseImage() || createImage.call(X);
                },
            });
            platform.setGlobalCanvas(X);
        }
        // #endregion set main screen implement
        // #region set secondary screen implement
        // -------  ---------
        if (mode === "poster") {
            this.Y = this.X;
            this.YC = this.XC;
        }
        else {
            let ofsResult;
            if (typeof ofsSelector === "string" && ofsSelector !== "") {
                ofsResult = await getCanvas(ofsSelector, component);
                ofsResult.canvas.width = this.W;
                ofsResult.canvas.height = this.H;
                this.setModel("C");
            }
            else {
                ofsResult = getOfsCanvas({ width: this.W, height: this.H });
                this.setModel("O");
            }
            this.Y = ofsResult.canvas;
            this.YC = ofsResult.context;
        }
        // #endregion set secondary screen implement
        // #region clear main screen implement
        // -------  -------
        // FIXME:
        if (model.clear === "CL") {
            this.clearContainer = () => {
                const { W, H } = this;
                this.XC.clearRect(0, 0, W, H);
            };
        }
        else {
            this.clearContainer = () => {
                const { W, H } = this;
                this.X.width = W;
                this.X.height = H;
            };
        }
        // #endregion clear main screen implement
        if (mode === "poster") {
            this.clearSecondary = this.stick = noop;
        }
        else {
            // #region clear secondary screen implement
            // -------  --------
            switch (model.clear) {
                case "CR":
                    this.clearSecondary = () => {
                        const { W, H } = this;
                        // FIXME: OffscreenCanvas 
                        const { canvas, context } = getOfsCanvas({ width: W, height: H });
                        this.Y = canvas;
                        this.YC = context;
                    };
                    break;
                case "CL":
                    this.clearSecondary = () => {
                        const { W, H } = this;
                        // FIXME:CanvasOffscreenCanvas
                        this.YC.clearRect(0, 0, W, H);
                    };
                    break;
                default:
                    this.clearSecondary = () => {
                        const { W, H, Y } = this;
                        Y.width = W;
                        Y.height = H;
                    };
            }
            // #endregion clear secondary screen implement
        }
    }
    /**
     * 
     * @param images
     */
    updateDynamicImages(images) {
        this.IM.appendAll(images);
    }
    /**
     * 
     * @param images 
     * @param filename 
     * @returns
     */
    loadImages(images, filename) {
        return this.IM.loadAll(images, filename);
    }
    /**
     * 
     * @returns
     */
    getImageData() {
        return this.XC.getImageData(0, 0, this.W, this.H);
    }
    /**
     * 
     * @param contentMode
     * @param videoSize
     * @returns
     */
    calculateScale(contentMode, videoSize) {
        const { Y } = this;
        const imageRatio = videoSize.width / videoSize.height;
        const viewRatio = Y.width / Y.height;
        const isAspectFit = contentMode === "aspect-fit" /* PLAYER_CONTENT_MODE.ASPECT_FIT */;
        const shouldUseWidth = (imageRatio >= viewRatio && isAspectFit) ||
            (imageRatio <= viewRatio && !isAspectFit);
        if (shouldUseWidth) {
            const scale = Y.width / videoSize.width;
            return {
                scaleX: scale,
                scaleY: scale,
                translateX: 0,
                translateY: (Y.height - videoSize.height * scale) / 2,
            };
        }
        const scale = Y.height / videoSize.height;
        return {
            scaleX: scale,
            scaleY: scale,
            translateX: (Y.width - videoSize.width * scale) / 2,
            translateY: 0,
        };
    }
    /**
     * 
     * @param contentMode
     * @param videoSize
     * @returns
     */
    resize(contentMode, videoSize) {
        const { width: canvasWidth, height: canvasHeight } = this.Y;
        const { width: videoWidth, height: videoHeight } = videoSize;
        const resizeKey = `${contentMode}-${videoWidth}-${videoHeight}-${canvasWidth}-${canvasHeight}`;
        if (this.lastResizeKey === resizeKey && this.lastTransform) {
            this.globalTransform = this.lastTransform;
            return;
        }
        let scale = {
            scaleX: 1,
            scaleY: 1,
            translateX: 0,
            translateY: 0,
        };
        if (contentMode === "fill" /* PLAYER_CONTENT_MODE.FILL */) {
            scale.scaleX = canvasWidth / videoWidth;
            scale.scaleY = canvasHeight / videoHeight;
        }
        else {
            scale = this.calculateScale(contentMode, videoSize);
        }
        this.lastResizeKey = resizeKey;
        this.globalTransform = this.lastTransform = {
            a: scale.scaleX,
            b: 0.0,
            c: 0.0,
            d: scale.scaleY,
            tx: scale.translateX,
            ty: scale.translateY,
        };
    }
    clearContainer = noop;
    clearSecondary = noop;
    /**
     * 
     */
    clearMaterials() {
        this.IM.release();
    }
    /**
     * 
     * @param videoEntity
     * @param currentFrame
     * @param start
     * @param end
     */
    draw(videoEntity, currentFrame, start, end) {
        const { materials, dynamicMaterials } = this.IM;
        render(this.YC, materials, dynamicMaterials, videoEntity, currentFrame, start, end, this.globalTransform);
    }
    stick() {
        const { W, H, mode } = this;
        if (mode !== "poster") {
            this.XC.drawImage(this.YC.canvas || this.Y, 0, 0, W, H);
        }
    }
    /**
     * 
     */
    destroy() {
        this.clearContainer();
        this.clearSecondary();
        this.clearMaterials();
        this.X = this.XC = this.Y = this.YC = null;
        this.clearContainer = this.clearSecondary = this.stick = noop;
    }
}
