import { platform } from "../platform";
const { noop } = platform;
/**
 * 
 */
export class Animator {
    /**
     * 
     */
    isRunning = false;
    /**
     * 
     */
    startTime = 0;
    /**
     * 
     */
    duration = 0;
    /**
     * 
     */
    loopStart = 0;
    /**
     * 
     */
    loopDuration = 0;
    /* ----  ---- */
    onStart = noop;
    onUpdate = noop;
    onEnd = noop;
    /**
     * 
     * @param duration
     * @param loopStart
     * @param loop
     * @param fillValue
     */
    setConfig(duration, loopStart, loop, fillValue) {
        this.duration = duration;
        this.loopStart = loopStart;
        this.loopDuration = duration * loop + fillValue - loopStart;
    }
    start() {
        this.isRunning = true;
        this.startTime = platform.now();
        this.onStart();
        this.doFrame();
    }
    resume() {
        this.isRunning = true;
        this.startTime = platform.now();
        this.doFrame();
    }
    pause() {
        this.isRunning = false;
        // 
        this.loopStart = (platform.now() - this.startTime + this.loopStart) % this.duration;
    }
    stop() {
        this.isRunning = false;
        this.loopStart = 0;
    }
    doFrame() {
        if (this.isRunning) {
            this.doDeltaTime(platform.now() - this.startTime);
            if (this.isRunning) {
                platform.rAF(() => this.doFrame());
            }
        }
    }
    doDeltaTime(DT) {
        const { duration: D, loopStart: LS, loopDuration: LD, } = this;
        // Percentage of speed time
        let TP;
        let ended = false;
        //   
        if (DT >= LD) {
            // 
            TP = 1.0;
            ended = true;
            this.stop();
        }
        else {
            //  =  / 
            TP = ((DT + LS) % D) / D;
        }
        this.onUpdate(TP);
        if (!this.isRunning && ended) {
            this.onEnd();
        }
    }
}
