import { platform } from "../platform";
import { Painter } from "../painter";
import { Animator } from "./animator";
import { Config } from "./config";
import benchmark from "../benchmark";
/**
 * SVGA 
 */
export class Player {
    /**
     * SVGA 
     * Video Entity
     */
    entity = undefined;
    /**
     * 
     */
    config = new Config();
    /**
     * 
     */
    animator = null;
    /**
     * 
     */
    painter = new Painter();
    // private isBeIntersection = true;
    // private intersectionObserver: IntersectionObserver | null = null
    /**
     * 
     * @param options 
     * @property container  Canvas 
     * @property secondary  Canvas 
     * @property loop  0
     * @property fillMode  animation-fill-mode forwards  fallbacks forwards
     * @property playMode  forwards  fallbacks  forwards
     * @property startFrame  0
     * @property endFrame  0
     * @property loopStartFrame  0
     */
    async setConfig(options, component) {
        const config = typeof options === "string" ? { container: options } : options;
        this.config.register(config);
        this.animator = new Animator();
        // 
        // this.setIntersectionObserver()
        await this.painter.register(config.container, config.secondary, component);
    }
    /**
     * 
     * @param key
     * @param value
     */
    setItem(key, value) {
        this.config.setItem(key, value);
    }
    // private setIntersectionObserver (): void {
    //   if (hasIntersectionObserver && this.config.isUseIntersectionObserver) {
    //     this.intersectionObserver = new IntersectionObserver(entries => {
    //       this.isBeIntersection = !(entries[0].intersectionRatio <= 0)
    //     }, {
    //       rootMargin: '0px',
    //       threshold: [0, 0.5, 1]
    //     })
    //     this.intersectionObserver.observe(this.config.container)
    //   } else {
    //     if (this.intersectionObserver !== null) this.intersectionObserver.disconnect()
    //     this.config.isUseIntersectionObserver = false
    //     this.isBeIntersection = true
    //   }
    // }
    /**
     *  SVGA 
     * @param videoEntity SVGA 
     * @returns Promise<void>
     */
    mount(videoEntity) {
        if (!videoEntity)
            throw new Error("videoEntity undefined");
        const { images, filename } = videoEntity;
        this.animator.stop();
        this.painter.clearSecondary();
        this.painter.clearMaterials();
        this.painter.updateDynamicImages(videoEntity.dynamicElements);
        this.entity = videoEntity;
        return this.painter.loadImages(images, filename);
    }
    /**
     * 
     * @param frame
     */
    onStart;
    /**
     * 
     * @param frame
     */
    onResume;
    /**
     * 
     * @param frame
     */
    onPause;
    /**
     * 
     * @param frame
     */
    onStop;
    /**
     * 
     * @param percent
     * @param frame
     * @param frames
     */
    onProcess;
    /**
     * 
     * @param frame
     */
    onEnd;
    /**
     * 
     */
    start() {
        this.startAnimation();
        this.onStart?.();
    }
    /**
     * 
     */
    resume() {
        this.animator.resume();
        this.onResume?.();
    }
    /**
     * 
     */
    pause() {
        this.animator.pause();
        this.onPause?.();
    }
    /**
     * 
     */
    stop() {
        this.animator.stop();
        this.painter.clearContainer();
        this.painter.clearSecondary();
        this.onStop?.();
    }
    /**
     * 
     */
    destroy() {
        this.animator.stop();
        this.painter.destroy();
        this.animator = null;
        this.entity = undefined;
    }
    /**
     * 
     * @param frame 
     * @param andPlay 
     */
    stepToFrame(frame, andPlay = false) {
        if (!this.entity || frame < 0 || frame >= this.entity.frames)
            return;
        this.pause();
        this.config.loopStartFrame = frame;
        if (andPlay) {
            this.start();
        }
    }
    /**
     * 
     * @param percent 
     * @param andPlay 
     */
    stepToPercentage(percent, andPlay = false) {
        if (!this.entity)
            return;
        const { frames } = this.entity;
        this.stepToFrame(Math.round((percent < 0 ? 0 : percent) * frames) % frames, andPlay);
    }
    /**
     * 
     */
    startAnimation() {
        const { entity, config, animator, painter } = this;
        const { now } = platform;
        const { fillMode, playMode, contentMode } = config;
        const { currFrame, startFrame, endFrame, totalFrame, spriteCount, aniConfig, } = config.getConfig(entity);
        const { duration, loopStart, loop, fillValue } = aniConfig;
        const isReverseMode = playMode === "fallbacks" /* PLAYER_PLAY_MODE.FALLBACKS */;
        // 
        let currentFrame = currFrame;
        // 
        let tail = 0;
        let nextTail;
        // 
        let latestFrame;
        // 
        let nextFrame;
        // 
        let exactFrame;
        // 
        let percent;
        // 
        // let partialDrawPercent: number;
        // 
        let hasRemained;
        // 
        animator.setConfig(duration, loopStart, loop, fillValue);
        painter.resize(contentMode, entity.size);
        // 
        const MAX_DRAW_TIME_PER_FRAME = 8;
        const MAX_ACCELERATE_DRAW_TIME_PER_FRAME = 3;
        const MAX_DYNAMIC_CHUNK_SIZE = 34;
        const MIN_DYNAMIC_CHUNK_SIZE = 1;
        // 
        let dynamicChunkSize = 4; // 
        let startTime;
        let chunk;
        let elapsed;
        // ``
        const patchDraw = (before) => {
            startTime = now();
            before();
            while (tail < spriteCount) {
                // nextTail
                chunk = Math.min(dynamicChunkSize, spriteCount - tail);
                nextTail = (tail + chunk) | 0;
                painter.draw(entity, currentFrame, tail, nextTail);
                tail = nextTail;
                // 
                elapsed = now() - startTime;
                if (elapsed < MAX_ACCELERATE_DRAW_TIME_PER_FRAME) {
                    dynamicChunkSize = Math.min(dynamicChunkSize * 2, MAX_DYNAMIC_CHUNK_SIZE); // 
                }
                else if (elapsed > MAX_DRAW_TIME_PER_FRAME) {
                    dynamicChunkSize = Math.max(dynamicChunkSize / 2, MIN_DYNAMIC_CHUNK_SIZE); // 
                    break;
                }
            }
        };
        // const TAIL_THRESHOLD_FACTOR = 1.05;
        // const TAIL_OFFSET = 2;
        // let partialDrawPercent = 0;
        // // 
        // const patchDraw = (before: () => void) => {
        //   before();
        //   if (tail < spriteCount) {
        //     // 1.15  2 
        //     nextTail = hasRemained
        //       ? Math.min(
        //           (spriteCount * partialDrawPercent * TAIL_THRESHOLD_FACTOR +
        //             TAIL_OFFSET) | 0,
        //           spriteCount
        //         )
        //       : spriteCount;
        //     if (nextTail > tail) {
        //       painter.draw(entity!, currentFrame, tail, nextTail);
        //       tail = nextTail;
        //     }
        //   }
        // };
        // 
        animator.onUpdate = (timePercent) => {
            benchmark.time("partial updated", () => {
                patchDraw(() => {
                    percent = isReverseMode ? 1 - timePercent : timePercent;
                    exactFrame = percent * totalFrame;
                    if (isReverseMode) {
                        nextFrame =
                            (timePercent === 0 ? endFrame : Math.ceil(exactFrame)) - 1;
                        // partialDrawPercent = Math.abs(1 - exactFrame + currentFrame);
                        // FIXME: 
                        percent = currentFrame / totalFrame;
                    }
                    else {
                        nextFrame = timePercent === 1 ? startFrame : Math.floor(exactFrame);
                        // partialDrawPercent = Math.abs(exactFrame - currentFrame);
                    }
                    hasRemained = currentFrame === nextFrame;
                });
            });
            if (hasRemained)
                return;
            painter.clearContainer();
            painter.stick();
            painter.clearSecondary();
            latestFrame = currentFrame;
            currentFrame = nextFrame;
            tail = 0;
            this.onProcess?.(~~(percent * 100) / 100, latestFrame);
        };
        animator.onStart = () => {
            entity.locked = true;
        };
        animator.onEnd = () => {
            entity.locked = false;
            // 
            if (fillMode === "none" /* PLAYER_FILL_MODE.NONE */) {
                painter.clearContainer();
            }
            this.onEnd?.();
        };
        animator.start();
    }
}
