import benchmark from "../benchmark";
import { platform } from "../platform";
export class ImagePool {
    /**
     *  img 
     */
    // FIXME: createImage/
    images = [];
    /**
     * 
     */
    dynamicMaterials = new Map();
    /**
     * 
     */
    materials = new Map();
    /**
     * 
     * @returns
     */
    getReleaseImage() {
        return this.images.shift();
    }
    /**
     * 
     * @param images 
     * @param filename 
     * @returns
     */
    loadAll(images, filename) {
        return benchmark.time("loadAll", () => {
            const { load, isImage } = platform.image;
            const imageAwaits = [];
            Object.keys(images).forEach((key) => {
                const image = images[key];
                if (isImage(image)) {
                    this.materials.set(key, image);
                }
                else {
                    const p = load(image, filename, key).then((img) => {
                        this.materials.set(key, img);
                    });
                    imageAwaits.push(p);
                }
            });
            return Promise.all(imageAwaits);
        });
    }
    /**
     * 
     * @param images
     */
    appendAll(images) {
        this.dynamicMaterials = new Map(Object.entries(images));
    }
    /**
     * 
     */
    release() {
        const { env } = platform.globals;
        const { isImage, isImageBitmap } = platform.image;
        // FIXME:  image  src  onload 
        if (env !== "alipay") {
            const releaseOne = (image) => {
                if (isImage(image)) {
                    image.onload = null;
                    image.onerror = null;
                    image.src = "";
                    this.images.push(image);
                }
                else if (isImageBitmap(image)) {
                    image.close();
                }
            };
            this.materials.forEach(releaseOne);
            this.dynamicMaterials.forEach(releaseOne);
        }
        this.materials.clear();
        this.dynamicMaterials.clear();
        this.images = Array.from(new Set(this.images));
    }
}
