const badge = "benchmark";
// 
const env = (() => {
    if (typeof window !== "undefined") {
        return "h5";
    }
    if (typeof tt !== "undefined") {
        return "tt";
    }
    if (typeof my !== "undefined") {
        return "alipay";
    }
    if (typeof wx !== "undefined") {
        return "weapp";
    }
    return "unknown";
})();
// 
const sys = (() => {
    if (env === "alipay") {
        return my.getDeviceBaseInfo().platform;
    }
    if (env === "tt") {
        return tt.getDeviceInfoSync().platform;
    }
    if (env === "weapp") {
        // @ts-ignore
        return wx.getDeviceInfo().platform;
    }
    return "unknown";
})().toLocaleLowerCase();
// 
const now = (() => {
    if (env === "h5") {
        return () => performance.now() / 1000;
    }
    if (env === "weapp") {
        // @ts-ignore
        return () => wx.getPerformance().now() / 1000;
    }
    if (env === "alipay") {
        return () => my.getPerformance().now() / 1000;
    }
    return () => Date.now();
})();
class Stopwatch {
    label = "";
    startTime = 0;
    isRealMachine = ["ios", "android", "openharmony"].includes(sys);
    start(label) {
        if (this.isRealMachine) {
            this.label = label;
            this.startTime = now();
        }
        else {
            console.time(label);
        }
    }
    stop(label) {
        if (this.isRealMachine) {
            console.log(`${this.label}: ${now() - this.startTime}`);
        }
        else {
            console.timeEnd(label);
        }
    }
}
export default {
    label(label) {
        console.log(badge, label);
    },
    async time(label, callback) {
        const stopwatch = new Stopwatch();
        stopwatch.start(label);
        const result = await callback();
        stopwatch.stop(label);
        return result;
    },
    line(size = 40) {
        console.log("-".repeat(size));
    },
    log(...message) {
        console.log(badge, ...message);
    },
};
